@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Parisienne&display=swap');

.promo{
    width:100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background:linear-gradient(-160deg,#383838,#000000);
    padding: 160px 0;
    border-style:solid;
    border-right:none;
    border-left:none;
    border-width:1px;
    border-image: linear-gradient(to right, #7a6844, #e6e4a7, #7a6844) 1 8%;
}

.promo__logo{
    width:120px;
}

.promo__title{
    width:40%;
    background: linear-gradient(90deg, #7a6844, #e6e4a7, #7a6844);
    font-family: 'Montserrat';
    font-size: 45px;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    letter-spacing: 4px;
    margin:20px 0 0;
}

.promo__caption{
    width:40%;
    background: linear-gradient(90deg, #7a6844, #e6e4a7, #7a6844);
    font-family: 'Parisienne';
    font-size: 40px;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin:20px 0 0
}